import Vue from 'vue'

function request(url, params={}, baseUrl='https://c.y.qq.com/') {
  url = baseUrl + url
  return Vue.jsonp(url, {
    callbackQuery: 'jsonpCallback',
    format: 'jsonp',
    inCharset: 'utf-8',
    outCharset: 'utf-8',
    ...params,
  })
}
export const search = (key) => {
  return request('splcloud/fcgi-bin/smartbox_new.fcg', {
    key,
  })
}
export const rankSongs = (topid) => {
  return request('v8/fcg-bin/fcg_v8_toplist_cp.fcg', {
    topid,
  })
}
export const rankList = () => {
  return request('v8/fcg-bin/fcg_myqq_toplist.fcg', {
    g_tk: 5381,
    platform: 'h5',
    needNewCode: 1,
    _: new Date().getTime()
  })
}
export const album = (albummid) => {
  return request('v8/fcg-bin/fcg_v8_album_info_cp.fcg', {
    albummid,
  })
}
export const singerInfo = (singermid) => {
  return request('v8/fcg-bin/fcg_v8_singer_track_cp.fcg', {
    singermid,
  })
}
export const hotkey = () => {
  return request('splcloud/fcgi-bin/gethotkey.fcg')
}
export const homeData = () => {
  return request('musichall/fcgi-bin/fcg_yqqhomepagerecommend.fcg')
}
export const lyric = (musicid) => {
  return request('musichall/lyric/fcgi-bin/fcg_query_lyric.fcg', {
    musicid
  })
}
export const cd = (disstid) => {
  return request('qzone/fcg-bin/fcg_ucc_getcdinfo_byids_cp.fcg', {
    disstid,
    type: 1,
    json: 1,
    utf8: 1,
    onlysong: 0,
    platform: 'yqq',
    format: 'jsonp',
  })
}
export const firstData = () => {
  return request('v8/fcg-bin/fcg_first_yqq.fcg', {
    tpl: 'v12',
    page: 'other',
  })
}
export const lastAlbum = () => {
  return request('v8/fcg-bin/album_library?g_tk=1278911659&hostUin=0&notice=0&platform=yqq&needNewCode=0&cmd=firstpage&page=0&pagesize=10&sort=1&language=-1&genre=0&year=1&pay=0&type=-1&company=-1')
}
export const recomPlaylist = () => {
  return request('cgi-bin/musicu.fcg', {
    data: '{"recomPlaylist":{"method":"get_hot_recommend","param":{"async":1,"cmd":2},"module":"playlist.HotRecommendServer"}}'
  },'https://u.y.qq.com/')
}




