<template>
  <div class="min-player">
    <div class="bar">
      <div class="wrap">
        <van-loading v-if="loading"/>
        <img v-else src="http://temp.im/64x64" alt="">
      </div>
      <div v-if="getInfo" class="info" @click="showDetail">
        <h2>{{getInfo.name}}</h2>
        <p>{{getInfo.singer}}</p>
      </div>
      <div class="ctrl">
        <van-icon :name="play ? 'pause-circle-o' : 'play-circle-o'" @click="turnHandle" />
        <van-icon :name="fold ? 'descending' : 'ascending'" @click="showList"/>
      </div>  
    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
import { mapMutations, mapState, mapGetters } from 'vuex'
export default {
  data() {
    return {
    }
  },
  methods: {
    ...mapMutations('player', ['showList', 'showDetail', 'toggle']),
    turnHandle() {
      this.toggle()
    },
    foldHandle() {
      this.fold = !this.fold
    },
  },
  computed: {
    ...mapState({
      fold: state => state.player.showList,
      play: state => state.player.isPlay,
      loading: state => state.player.loading,
    }),
    ...mapGetters('player', ['getInfo']),
  },
}
</script>
<style lang="scss" scoped>
.min-player {
  position: fixed;
  width: 100%;
  bottom: 0;
  left: 0;
  background-color: #fff;
  .bar {
    height: 1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .wrap {
      flex: 0 0 1rem;
      .van-loading {
        text-align: center; }
      img {
        display: block;
        width: 100%; } }
    .info {
      flex: auto;
      margin-left: .2rem;
      h2 {
        font-size: 16px;
        margin-bottom: .1rem; }
      p {
        font-size: 14px;
        color: #666; } }
    .ctrl {
      flex: 0 0 70px;
      .van-icon {
        font-size: .5rem;
      } } } }
</style>



