import { Dialog } from 'vant'
import { Toast } from 'vant'

const state = {
  showList: false,
  showDetail: false,
  isPlay: false,
  songList: [],
  currentIndex: -1,
  loading: true,
  player: new QMplayer({
    onplay: function () {
      state.loading = false
    },
  }),
}
const mutations = {
  showList(state) {
    state.showList = !state.showList
  },
  showDetail(state) {
    // state.showDetail = !state.showDetail
    Toast('歌词开发中')
  },
  songList(state, val) {
    state.songList = Array.isArray(val) ? val : [val]
    state.currentIndex = -1
  },
  removeSongList(state, idx) {
    if (state.currentIndex === idx) {
      state.player.pause()
    }
    state.songList.splice(idx, 1)
    state.isPlay = false
  },
  currentIndex(state, val) {
    state.currentIndex = val
  },
  toggle(state) {
    state.isPlay = !state.isPlay
    state.player.toggle()
  },
}
const getters = {
  songIdList(state) {
    return state.songList.map(e => e.mid)
  },
  getInfo(state) {
    return state.songList[state.player.data.index]
  },
}
const actions = {
  play({ state, getters }, idx=0) {
    if (idx === state.currentIndex) return
    state.currentIndex = idx
    state.player.play(getters.songIdList, {
      index: state.currentIndex
    })
    state.loading = true
    state.isPlay = true
    let timer = setTimeout(_=> {
      if (state.loading) {
        Dialog.alert({
          message: '加载失败！',
        }).then(_=> {
          state.currentIndex = -1
        })
      }
      clearTimeout(timer)
    }, 5000)
  },
}
export default {
  namespaced: true,
  actions,
  state,
  getters,
  mutations,
}


