<template>
  <div class="player-list">
    <van-nav-bar
      :title="`播放列表(${songList.length})`"
      right-text="关闭"
      @click-right="showList"
    />
    <scroll-view class="wrap">
      <ul>
        <li :class="{active: i === currentIndex}" v-for="(e, i) in songList" :key="i">
          <p @click="turnOn(i)">{{e.name}}<span>{{e.singer}}</span></p>
          <van-icon name="cross" @click="remove(i)"/>
        </li>
      </ul>
    </scroll-view>
  </div>
</template>
<script>
import ScrollView from '#/Common/ScrollView'
import { mapMutations, mapState, mapActions } from 'vuex'
export default {
  components: {
    ScrollView,
  },
  methods: {
    ...mapMutations('player', ['showList', 'showDetail', 'removeSongList']),
    ...mapActions('player', ['play']),
    remove(i) {
      this.$dialog.confirm({
        message: '确定把歌曲从当前播放列表移除？'
      }).then(_=> {
        this.removeSongList(i)
      }).catch(_=> {

      })
    },
    turnOn(i) {
      this.play(i)
    },
  },
  computed: {
    ...mapState('player', ['songList', 'currentIndex'])
  },
}
</script>
<style lang="scss" scoped>
/deep/ .van-icon-cross:before {
  position: absolute;
  top: 50%;
  left: -.4rem;
  transform: translateY(-50%); }
.player-list {
  z-index: 3;
  position: fixed;
  width: 100%;
  height: 100vh;
  bottom: 0;
  left: 0;
  background-color: #fff;
  .wrap {
    height: 100vh;
    overflow: hidden;
    li {
      display: flex;
      height: .8rem;
      line-height: .8rem;
      justify-content: space-between;
      font-size: 15px;
      padding: 0 .2rem;
      border-bottom: 1px solid #f6f6f6;
      &.active {
        color: #ff0000;
        background-color: #f6f6f6;
        span {
          color: #ff0000; } }
      span {
        color: #aaa;
        margin-left: .2rem;
        font-size: 14px; } } } }
</style>



