import * as api from '@/api'
import Store from '@/store'

const state = {
  rankData: null,
  loading: true,
  rankList: [],
  mvData: [],
  slider: [],
  hotKey: [],
  historySearch: [],
}
const mutations = {
  rankData(state, val) {
    state.rankData = val
  },
  loading(state, val) {
    state.loading = val
  },
  rankList(state, val) {
    state.rankList = val
  },
  mvData(state, val) {
    state.mvData = val
  },
  slider(state, val) {
    state.slider = val
  },
  hotKey(state, val) {
    state.hotKey = val
  },
  addHistorySearch(state, val) {
    state.historySearch.push(val)
  },
  clearHistorySearch(state) {
    state.historySearch = []
  },
  deleteHistorySearch(state, index) {
    state.historySearch.splice(index, 1)
  },
}
const getters = {
  songList(state) {
    return state.rankData && state.rankData.songlist || []
  },
  topInfo(state) {
    return state.rankData && state.rankData.topinfo
  },
  topList(state) {
    return state.rankList.topList
  },
}
const actions = {
  rankSongs({ commit }, id) {
    commit('loading', true)
    commit('rankData', null)
    api.rankSongs(id).then(res => {
      if (!res.code) {
        let { songlist, topinfo } = res
        commit('rankData', { songlist, topinfo })
        Store.commit('player/songList', songlist.map(e => {
          return {
            mid: e.data.albummid,
            name: e.data.songname,
            singer: e.data.singer[0].name
          }
        }))
      }
      setTimeout(_=> {
        commit('loading', false)
      }, 500)
    })
  },
  rankList({ commit }) {
    api.rankList().then(res => {
      if (!res.code) {
        commit('rankList', res.data)
      }
    })
  },
  homeData({ commit }) {
    Promise.all([api.homeData(), api.firstData()]).then(res => {
      let [a, b] = res
      if (!a.code) {
        commit('slider', a.data.slider)
      }
      if (!b.code) {
        commit('mvData', b.data.shoubomv)
      }
    })
  },
  hotkey({ commit }) {
    api.hotkey().then(res => {
      if (!res.code) {
        commit('hotKey', res.data)
      }
    })
  },
}
export default {
  namespaced: true,
  state,
  mutations,
  getters,
  actions,
}


