<template>
  <div class="player">
    <div v-if="getInfo" class="title">
      <van-icon name="arrow-left" @click="showDetail"/>
      <h2>{{getInfo.name}}</h2>
      <p>{{getInfo.singer}}</p>
    </div>
    <scroll-view class="lyric">
      <ul>
        <li v-for="(e, i) in 40" :key="i">{{i}}</li>
      </ul>
    </scroll-view>
    <div class="ctrl">
      `
    </div>
  </div>
</template>
<script>
import ScrollView from '#/Common/ScrollView'
import { mapMutations, mapState, mapGetters } from 'vuex'
export default {
  name: 'Player',
  components: {
    ScrollView,
  },
  data() {
    return {
    }
  },
  methods: {
    ...mapMutations('player', ['showList', 'showDetail']),
  },
  computed: {
    ...mapGetters('player', ['getInfo']),
  },
}
</script>
<style lang="scss" scoped>
.player {
  z-index: 2;
  position: fixed;
  top: 0;
  width: 100vw;
  height: 100vh;
  background-color: #fff;
  .title {
    overflow: hidden;
    height: 20vh;
    .van-icon-arrow-left {
      margin: .2rem; }
    h2 {
      margin-bottom: .2rem;
      font-size: 20px;
      text-align: center; }
    p {
      font-size: 16px;
      text-align: center; } }
  .lyric {
    overflow: hidden;
    height: 50vh;
    text-align: center;
    font-size: 16px; }
  .ctrl {
    height: 30vh; } }
</style>



